
:host{
  font-size: 10px;
}
.dav{
  &-footer{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 4px;
      padding: 0 8px;
      &__textfield{
          width: 100%;
      }
      &__icon{
          margin-left: 8px;
          background-color: $red_primary;
          width: 40px;
          min-width: 40px;
          height: 40px;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          cursor: pointer;
          &__img{
              width: 24px;
              height: 24px;
          }
          &__img-send{
              width: 28px;
              height: 28px;
              margin-right: 4px;
          }
          &__inactive{
            @extend .dav-footer__icon;
            background-color: $option_hover;
          }
      }
      
    }
      &__equalizer{
          width: 60px;
          height: 50px;
      }
  }
.noPadding{
  padding-bottom: 0px;
}