import { Component, h, State, } from '@stencil/core';
import state from '../../store/store';
import { Sender } from '../chat-component/enums/sender.enum';
 
@Component({
  tag: 'chat-footer',
  styleUrl: 'chat-footer.scss',
  shadow: true
})
export class ChatFooter {
  @State() value: string;
  handleSubmit(e) {
    e.preventDefault()
    state.messages =[...state.messages,{text:this.value,sender:Sender.USER}]
    this.value=""
  }
  handleChange(event) {
    this.value = event.target.value;
  }
  render() {
    //const footerClass = !this.isListening ? 'dav-footer' : ;
    const buttonClass = this.value ? "dav-footer__icon" : 'dav-footer__icon__inactive';

    return (
      <form onSubmit={(e) => this.handleSubmit(e)} class='dav-footer noPadding'>
        {/* <app-textfield class="dav-footer__textfield" placeholder='Mensaje' ></app-textfield> */}
        <input type="text" value={this.value} onInput={(event) => this.handleChange(event)} />
        <button class={buttonClass} type="submit">
          <img class='dav-footer__icon__img-send' src='../../assets/icons/extra-icons/icon-send-message.svg' />
        </button>
      </form>
    );
  }
}
